body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  box-sizing: border-box;
}

/* Create two equal columns that floats next to each other */
.column {
  text-align: center;
  float: left;
  width: 50%;
  padding: 31px;
  height: auto;
}
.form-group {
  width: 100%;
  padding: 15px;
}
.form-input {
  background-color: #b3b4b529 !important;
  width: 70%;
  border: none !important;
  font-size: 19px !important;
}
.margin-left-7{
  margin-left:7%;
}
.form-input::placeholder {
  margin:1px;
  padding:10px;
}
/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
.row{
  background-color: white;
}
.image-uploader{
  margin: auto;
  text-align: center;
  border: 4px dashed #c3c4c7;
  width: 70%;
  padding: 13px;
  cursor: pointer;
}
.btn{
  width: 46%;
  padding: 4px;
  margin-top: 18px;
}
.btn-float-left{
  float: left;
}.btn-float-right{
   float: right;
 }

.from-btn-container{
  width: 70%;
  margin: auto;
}
.plus{
  font-size: 100px;
  line-height: 1.5em;
  margin: 0;
  padding: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-family: Frutiger, "Frutiger Linotype", Univers, Calibri, "Gill Sans", "Gill Sans MT", "Myriad Pro", Myriad, "DejaVu Sans Condensed", "Liberation Sans", "Nimbus Sans L", Tahoma, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: lighter;
  color: #9f9f9f;
  border: 1px dashed #c3c4c7;
}
.plus::before {
  display: block;
  content: '+';
}
.form-group-option{
  padding: 28px;
  width: 50%;
  margin: auto;
  text-align: center;
}
.phone-number-type{
  margin: auto;
  width: 70%;
  padding: 13px;
}
.form-group-option button{
  width: 20%;
  padding: 4px;
  margin-top: 18px;
}

